"use client";
import React, { useState, useEffect } from "react";
import Sidebar from "../components/Sidebar";

// Placeholder untuk notifikasi toast jika tidak menggunakan react-toastify
const toast = {
  success: (message: string) => console.log("Sukses:", message),
  error: (message: string) => console.error("Error:", message),
};

// Mendefinisikan tipe untuk Alat dan Catatan Peminjaman
type Tool = {
  id: number;
  item_name: string;
  item_code: string;
  item_status: string;
  quantity: number;
  category: string;
};

type BorrowRecord = {
  id: number;
  user_id: number;
  user_name: string;
  tool_id: number;
  tool_name: string;
  quantity: number;
  borrow_date: string;
  return_date: string | null;
  status: "borrowed" | "returned";
};

// Data dummy untuk catatan peminjaman
const mockBorrowRecords: BorrowRecord[] = [
  { id: 1, user_id: 1, user_name: "Elfan", tool_id: 101, tool_name: "kamera canon", quantity: 1, borrow_date: "2025-07-02", return_date: "2025-07-02", status: "returned" },
  { id: 2, user_id: 2, user_name: "Rahmat", tool_id: 101, tool_name: "kamera canon", quantity: 2, borrow_date: "2025-07-01", return_date: null, status: "borrowed" },
  { id: 3, user_id: 3, user_name: "Toriq", tool_id: 102, tool_name: "kain warna merah", quantity: 4, borrow_date: "2025-07-01", return_date: null, status: "borrowed" },
  { id: 4, user_id: 4, user_name: "Galih", tool_id: 102, tool_name: "kain warna merah", quantity: 1, borrow_date: "2025-07-01", return_date: null, status: "borrowed" },
  { id: 6, user_id: 5, user_name: "Muzzaky", tool_id: 101, tool_name: "kamera canon", quantity: 1, borrow_date: "2025-07-02", return_date: null, status: "borrowed" },
  { id: 7, user_id: 6, user_name: "Rais", tool_id: 102, tool_name: "kain warna merah", quantity: 3, borrow_date: "2025-07-02", return_date: null, status: "borrowed" },
  { id: 10, user_id: 7, user_name: "Defa", tool_id: 101, tool_name: "kamera canon", quantity: 4, borrow_date: "2025-07-02", return_date: null, status: "borrowed" },
  { id: 11, user_id: 1, user_name: "Elfan", tool_id: 102, tool_name: "kain warna merah", quantity: 1, borrow_date: "2025-07-01", return_date: "2025-07-02", status: "returned" },
  { id: 13, user_id: 8, user_name: "Ramdan", tool_id: 101, tool_name: "kamera canon", quantity: 2, borrow_date: "2025-07-07", return_date: null, status: "borrowed" }
];


// Komponen BorrowCard untuk menampilkan catatan peminjaman individu
function BorrowCard({
  record,
}: {
  record: BorrowRecord;
}) {
  return (
    <div className="bg-white rounded-lg shadow-md overflow-hidden border border-gray-200">
      <div className="p-5">
        <div className="flex justify-between items-start mb-4">
          <div>
            <h2 className="text-lg font-semibold text-gray-800">
              {record.tool_name}
            </h2>
            <p className="text-sm text-gray-600">
              Dipinjam oleh: {record.user_name}
            </p>
          </div>
          <span
            className={`px-2 py-1 text-xs rounded-full ${
              record.status === "borrowed"
                ? "bg-yellow-100 text-yellow-800"
                : "bg-green-100 text-green-800"
            }`}
          >
            {record.status}
          </span>
        </div>

        <div className="grid grid-cols-2 gap-4 text-sm">
          <div>
            <p className="text-gray-500">Jumlah</p>
            <p className="font-medium">{record.quantity}</p>
          </div>
          <div>
            <p className="text-gray-500">Tanggal Peminjaman</p>
            <p className="font-medium">
              {new Date(record.borrow_date).toLocaleDateString()}
            </p>
          </div>
          <div>
            <p className="text-gray-500">Tanggal Pengembalian</p>
            <p className="font-medium">
              {record.return_date
                ? new Date(record.return_date).toLocaleDateString()
                : "Belum dikembalikan"}
            </p>
          </div>
        </div>
      </div>
      {/* Tombol "Tandai sebagai Dikembalikan" dihapus karena fitur penambahan/pengembalian dihilangkan */}
    </div>
  );
}

// Komponen utama ToolBorrowingSystem
export default function ToolBorrowingSystem() {
  const [borrowRecords, setBorrowRecords] = useState<BorrowRecord[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [search, setSearch] = useState("");
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage] = useState(8);

  // Efek untuk mengambil data catatan peminjaman (menggunakan data dummy)
  useEffect(() => {
    // Simulasikan pengambilan data asinkron
    setLoading(true);
    setError(null);
    setTimeout(() => {
      setBorrowRecords(mockBorrowRecords);
      setLoading(false);
    }, 500); // Penundaan singkat untuk mensimulasikan loading
  }, []); // Array dependensi kosong berarti efek ini berjalan sekali saat komponen dimuat

  // Filter catatan peminjaman berdasarkan input pencarian
  const filteredRecords = borrowRecords.filter(
    (record) =>
      record.user_name.toLowerCase().includes(search.toLowerCase()) ||
      record.tool_name.toLowerCase().includes(search.toLowerCase()) ||
      record.status.toLowerCase().includes(search.toLowerCase())
  );

  // Logika paginasi
  const indexOfLastItem = currentPage * itemsPerPage;
  const indexOfFirstItem = indexOfLastItem - itemsPerPage;
  const currentItems = filteredRecords.slice(indexOfFirstItem, indexOfLastItem);
  const totalPages = Math.ceil(filteredRecords.length / itemsPerPage);

  // UI status memuat
  if (loading) {
    return (
      <div className="min-h-screen bg-gray-50 flex">
        {/* <Sidebar /> */} {/* Komponen Sidebar tidak disediakan */}
        <main className="flex-1 p-8 ml-64 flex items-center justify-center">
          <div className="flex flex-col items-center">
            <div className="animate-spin rounded-full h-12 w-12 border-t-2 border-b-2 border-blue-500 mb-4"></div>
            <div className="text-gray-600">Memuat data...</div>
          </div>
        </main>
      </div>
    );
  }

  // UI status kesalahan (tidak akan terjadi tanpa API, tetapi dipertahankan untuk fleksibilitas)
  if (error) {
    return (
      <div className="min-h-screen bg-gray-50 flex">
        {/* <Sidebar /> */} {/* Komponen Sidebar tidak disediakan */}
        <main className="flex-1 p-8 ml-64 flex flex-col items-center justify-center">
          <div className="bg-white p-6 rounded-xl shadow-md max-w-md w-full text-center">
            <div className="text-red-500 mb-4">
              <svg
                className="w-16 h-16 mx-auto"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
                />
              </svg>
            </div>
            <h3 className="text-lg font-medium text-gray-900 mb-2">
              Terjadi Kesalahan
            </h3>
            <p className="text-gray-600 mb-6">{error}</p>
            <button
              onClick={() => window.location.reload()}
              className="px-4 py-2 bg-blue-600 text-white rounded-lg font-medium hover:bg-blue-700"
            >
              Coba Lagi
            </button>
          </div>
        </main>
      </div>
    );
  }

  // Konten utama Sistem Peminjaman Alat
  return (
    <div className="min-h-screen bg-gray-50 flex">
      {/* <Sidebar /> */} {/* Komponen Sidebar tidak disediakan */}
      <main className="flex-1 p-8 ml-64">
        <div className="flex flex-col md:flex-row md:items-center md:justify-between mb-8 gap-4">
          <div className="flex-1">
            <h1 className="text-3xl font-bold text-gray-800 mb-1">
              Sistem Peminjaman Alat
            </h1>
            <p className="text-gray-500">Tampilan data peminjaman alat</p>
          </div>
          {/* Tombol "Pinjam Alat" dihapus */}
        </div>

        <div className="mb-6">
          <div className="relative max-w-md">
            <div className="absolute inset-y-0 left-0 pl-3 flex items-center pointer-events-none">
              <svg
                className="w-5 h-5 text-gray-400"
                fill="none"
                stroke="currentColor"
                viewBox="0 0 24 24"
              >
                <path
                  strokeLinecap="round"
                  strokeLinejoin="round"
                  strokeWidth="2"
                  d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"
                />
              </svg>
            </div>
            <input
              type="text"
              placeholder="Cari catatan..."
              value={search}
              onChange={(e) => setSearch(e.target.value)}
              className="pl-10 pr-4 py-2.5 w-full rounded-lg border border-gray-300 focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
            />
          </div>
        </div>

        {filteredRecords.length === 0 ? (
          <div className="bg-white rounded-xl shadow-sm p-8 text-center">
            <svg
              className="w-16 h-16 mx-auto text-gray-400 mb-4"
              fill="none"
              stroke="currentColor"
              viewBox="0 0 24 24"
            >
              <path
                strokeLinecap="round"
                strokeLinejoin="round"
                strokeWidth="2"
                d="M9.172 16.172a4 4 0 015.656 0M9 10h.01M15 10h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z"
              />
            </svg>
            <h3 className="text-lg font-medium text-gray-900 mb-1">
              {borrowRecords.length === 0
                ? "Tidak ada catatan peminjaman ditemukan"
                : "Tidak ada catatan yang cocok ditemukan"}
            </h3>
            <p className="text-gray-500 mb-4">
              {borrowRecords.length === 0
                ? "Tidak ada catatan untuk ditampilkan."
                : "Coba istilah pencarian yang berbeda"}
            </p>
          </div>
        ) : (
          <>
            <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-4 gap-6">
              {currentItems.map((record) => (
                <BorrowCard
                  key={record.id}
                  record={record}
                />
              ))}
            </div>

            {totalPages > 1 && (
              <div className="flex justify-center mt-8">
                <nav className="inline-flex rounded-md shadow-sm -space-x-px">
                  <button
                    onClick={() =>
                      setCurrentPage((prev) => Math.max(1, prev - 1))
                    }
                    disabled={currentPage === 1}
                    className={`px-3 py-2 rounded-l-md border ${
                      currentPage === 1
                        ? "bg-gray-100 border-gray-300 text-gray-400 cursor-not-allowed"
                        : "bg-white border-gray-300 text-gray-500 hover:bg-gray-50"
                    }`}
                  >
                    Sebelumnya
                  </button>
                  {Array.from({ length: totalPages }, (_, i) => i + 1).map(
                    (number) => (
                      <button
                        key={number}
                        onClick={() => setCurrentPage(number)}
                        className={`px-4 py-2 border ${
                          currentPage === number
                            ? "bg-blue-50 border-blue-500 text-blue-600"
                            : "bg-white border-gray-300 text-gray-500 hover:bg-gray-50"
                        }`}
                      >
                        {number}
                      </button>
                    )
                  )}
                  <button
                    onClick={() =>
                      setCurrentPage((prev) => Math.min(totalPages, prev + 1))
                    }
                    disabled={currentPage === totalPages}
                    className={`px-3 py-2 rounded-r-md border ${
                      currentPage === totalPages
                        ? "bg-gray-100 border-gray-300 text-gray-400 cursor-not-allowed"
                        : "bg-white border-gray-300 text-gray-500 hover:bg-gray-50"
                    }`}
                  >
                    Berikutnya
                  </button>
                </nav>
              </div>
            )}
          </>
        )}
      </main>
    </div>
  );
}
